import Vue from 'vue'
import VueRouter from 'vue-router'

import app from '../firebase'

Vue.use(VueRouter)

const routes = [{
    path: '/sesion',
    name: 'sesion',
    // route level code-splitting
    // this generates a separate chunk (sesion.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "sesion" */ '../views/Sesion.vue')
  },
  {
    path: '/registro',
    name: 'registro',
    // route level code-splitting
    // this generates a separate chunk (resgistrar.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "resgistrar" */ '../views/Registro.vue')
  },
  {
    path: '/',
    name: 'Inicio',
    // route level code-splitting
    // this generates a separate chunk (inicio.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "inicio" */ '../views/Inicio.vue')
  },
  {
    path: '/servicios',
    name: 'Servicios',
    meta: {
      requiresAuth: true
    },
    // route level code-splitting
    // this generates a separate chunk (servicios.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "servicios" */ '../views/Servicios.vue')
  },
  {
    path: '/pqrs',
    name: 'PQRS',
    // route level code-splitting
    // this generates a separate chunk (pqrs.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "pqrs" */ '../views/PQRS.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})


router.beforeEach((to, from, next) => {



  if (to.matched.some(record => record.meta.requiresAuth)) {
    // this route requires auth, check if logged in
    // if not, redirect to login page.
    const user = app.auth().currentUser;

    if (user == null) {
      next({
        name: 'sesion'
      })
    } else {
      next()
    }
  } else {
    next() // make sure to always call next()!
  }
})




